import React, { Component, Fragment } from 'react'
import PropTypes from 'prop-types'
import './Sidebar01.scss';
import { sacmaumoi } from 'assets/img';
import * as ROUTES from 'constants/Routes';
// import { Link } from 'react-router-dom';
// import { NavItem } from 'reactstrap';

const propsTypes = {
  isClicked: PropTypes.bool,
  clickHandle: PropTypes.func,
}

let sidebar;
let overlay;

export class Sidebar01 extends Component {

  openSidebar = () => {
    sidebar.style.transform = "translateX(0px)";
    // sidebar.style.opacity = "1";
    overlay.style.display = "block";
  }

  closeSidebar = () => {
    sidebar.style.transform = "translateX(-280px)";
    // sidebar.style.opacity = "0";
    overlay.style.display = "none";
  }

  render() {

    return (
      <Fragment>
        <div id="sidebar01" className="SideNav">
          <div className="SideNav__Menu__Header">
            <img className="Header__Logo mr-2" src={sacmaumoi} alt="Sắc màu mới" />
          </div>
          <hr />
          <a onClick={() => { this.closeSidebar(); this.props.clickHandle(); }} className="SideNav__Link btn btn-success themDonHangStyle"  href={ROUTES.addOrder}>Thêm đơn hàng</a>
          <hr />
          <a onClick={() => { this.closeSidebar(); this.props.clickHandle(); }} className="SideNav__Link" href={ROUTES.orderManage}>Quản lý đơn hàng</a>
          <hr />
          <a onClick={() => { this.closeSidebar(); this.props.clickHandle(); }} className="SideNav__Link" href={ROUTES.statistics}>Thống Kê</a>
          <hr />
          <a onClick={() => { this.closeSidebar(); this.props.clickHandle(); }} className="SideNav__Link" href={ROUTES.schedulesDelivery}>Lịch giao hàng</a>
        </div>
        <div id="sidebar_overlay" onClick={this.props.clickHandle}>
        </div>
      </Fragment>
    )
  }

  componentDidMount = () => {
    sidebar = document.getElementById("sidebar01");
    overlay = document.getElementById("sidebar_overlay");
    // let sidebar = document.getElementById("sidebar01");
    // sidebar.addEventListener('touchmove', (e) => {
    //   e.preventDefault();
    // }, false)

    // let overlay = document.getElementById("sidebar_overlay");
    // overlay.addEventListener('touchmove', (e) => {
    //   e.preventDefault();
    // }, false)
  }

  componentWillUpdate = (nextProps, nextState) => {
    if (nextProps.isClicked === true) {
      this.openSidebar();
    } else {
      this.closeSidebar();
    }
  }
}

Sidebar01.propsTypes = propsTypes;

export default Sidebar01
