import React, { PureComponent } from 'react';
import { Link, withRouter } from 'react-router-dom';
import { DropdownItem, DropdownMenu, DropdownToggle, Nav, NavItem } from 'reactstrap';
import PropTypes from 'prop-types';
import { sacmaumoi } from '../../assets/img';
import { Sidebar01 } from 'components/_shared/Sidebar/Sidebar01/Sidebar01';
import * as ROUTES from 'constants/Routes';

import { AppHeaderDropdown, AppNavbarBrand } from '@coreui/react';
import './DefaultHeader.scss';
import { withFirebase } from 'Helpers/Firebase';
import { compose } from 'recompose';

const propTypes = {
  children: PropTypes.node,
};

const defaultProps = {};

class DefaultHeader extends PureComponent {
  state = {
    isClicked: false,
  }

  clickMenuIconHandle = () => {
    let newValue = !this.state.isClicked;
    this.setState({
      isClicked: newValue
    })
  }

  logOutHandle = () => {
    const { firebase } = this.props;
    firebase.doLogOut();
  }

  goToSettings = () => {
    const { history } = this.props;
    history.push(ROUTES.settings)
  }

  render() {
    const { firebase: { auth: { currentUser } } } = this.props;
    return (
      <React.Fragment>
        <span className="ml-3 menuSidebar" onClick={this.clickMenuIconHandle}>MENU</span>
        <AppNavbarBrand
          full={{ src: sacmaumoi, width: 89, alt: 'CoreUI Logo' }}
          minimized={{ src: sacmaumoi, width: 30, alt: 'CoreUI Logo' }}
        />

        <Nav className="d-md-down-none" navbar>
          <NavItem className="px-3">
            <Link to={ROUTES.addOrder} className="btn btn-success themDonHangStyle">Thêm Đơn Hàng</Link>
          </NavItem>
          <NavItem className="px-3">
            <Link to={ROUTES.orderManage}>Quản Lý Đơn Hàng</Link>
          </NavItem>
          <NavItem className="px-3">
            <Link to={ROUTES.statistics}>Thống kê</Link>
          </NavItem>
          <NavItem className="px-3">
            <Link to={ROUTES.schedulesDelivery}>Lịch giao hàng</Link>
          </NavItem>
        </Nav>
        <Nav className="ml-auto" navbar>
          <AppHeaderDropdown direction="down">
            <DropdownToggle nav>
              <img src={currentUser.photoURL} className="img-avatar mr-2" alt={currentUser.email} />
            </DropdownToggle>
            <DropdownMenu right style={{ right: 'auto' }}>
              <DropdownItem header tag="div" className="text-center">{currentUser.email}</DropdownItem>
              <DropdownItem onClick={this.goToSettings}><i className="fa fa-gear"></i> Settings</DropdownItem>
              <DropdownItem onClick={this.logOutHandle}><i className="fa fa-sign-out"></i> Sign Out</DropdownItem>
            </DropdownMenu>
          </AppHeaderDropdown>
        </Nav>
        <Sidebar01 isClicked={this.state.isClicked} clickHandle={this.clickMenuIconHandle} />
      </React.Fragment>
    );
  }
}

DefaultHeader.propTypes = propTypes;
DefaultHeader.defaultProps = defaultProps;

const enhanlers = compose(
  withRouter,
  withFirebase,
)

export default enhanlers(DefaultHeader);
